.primary-bg {
  background-color: #00a755 !important;
  border: #00a755;
  color: #fff;
}

.primary-color {
  color: #00a755;
}

.input__input-wrapper {
  width: 100%;
}


.table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 16px;
}

.table > thead {
  color: #6d7986;
  background-color: #ecedef;
  font-size: 13px;
  line-height: 18px;
}

.table > thead > tr > td {
  padding: 4px 8px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.table > thead > tr > td > div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.table > thead > tr > td > div > span > svg {
  display: block;
  margin: -4px;
}

.table > tbody > tr {
  position: relative;
  font-size: 13px;
  color: #172a3f;
  border-bottom: 1px solid rgba(11, 31, 53, 0.08);
}

.table > tbody > tr > td {
  padding: 14px 8px 10px 8px;
}

.table > tbody > .table-row-selected {
  background-color: #e6f7ff;
}

.table > tbody > .table-row-selected:hover {
  background-color: #ddf4ff;
}

.table__sorted {
  background-color: #f8f7f7;
}
.table__tr-loading {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: #e6f7ff;
  opacity: 0.8;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
}


.thumbnail {
  border: 1px solid #d8dcde;
  box-sizing: border-box;
  border-radius: 8px;
  margin-bottom: 20px;
}

.thumbnail__chart {
  padding: 18px;
  word-wrap: break-word;
  height: 100%;
}

.thumbnail__chart > h3 {
  color: #172a3f;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
}

.thumbnail__chart .thumbnail__footer {
  display: flex;
  justify-content: space-between;
}

.thumbnail__profit {
  color: #17bd77;
}
