.wrapper {
  width: 100%;
  overflow-x: auto;
  padding-bottom: 10px;
}

.table_wrapper {
  min-width: 100%;
}

.header_wrapper {
  width: 100%;
  background-color: #f3f4f5;
  display: grid;
}

.header_item {
  padding: 8px 16px;
  font-size: 13px;
  line-height: 18px;
  color: #6d7986;
}

.body_item {
  width: 100%;
  display: grid;

  font-size: 13px;
  line-height: 18px;
  color: #172a3f;
}

.body_item:hover {
  background-color: #fafafa;
}

.body_item div {
  padding: 8px 16px;
  border-bottom: 1px solid rgba(11, 31, 53, 0.08);
  word-break: break-all;
}

.body_item {
  transition: opacity 0.3s;
}

.skeleton {
  height: 21px;
}
