
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Roboto', sans-serif;
  font-style: normal;
}

button {
  cursor: pointer;
}

hr {
  border: none;
  background-color: #dbdee1;
  height: 1px;
}

.form-field{
  margin-bottom:16px
}

.navbar {
  margin-bottom: 32px;
}

.filter__header{
  display: flex;
  align-items: center;
  gap: 16px;
  margin-top: 24px;
  margin-bottom: 18px;
}
.filter__footer{
  display: flex;
  gap: 16px;
  margin-top: 24px;
  margin-bottom: 24px
}

.login__wrapper, .resetPassword__wrapper{
  display:flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.login__wrapper>form{
  margin-bottom:15%
}

.visible{
  visibility: visible;
}

.hide{
  visibility: none;
}

label{
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
}

label[data-type='secondary']{
  color: #858F9A;
}


.p-0 {
  padding: 0 !important;
}

.mb-20 {
  margin-bottom: 20px;
}
.mb-24 {
  margin-bottom: 24px;
}
.mb-32 {
  margin-bottom: 32px;
}
.mb-34 {
  margin-bottom: 34px;
}
.mb-40 {
  margin-bottom: 40px;
}
.mb-42 {
  margin-bottom: 42px;
}
.mr-32 {
  margin-right: 32px;
}
.ml-4 {
  margin-left: 4px;
}
.mt-24 {
  margin-top: 32px;
}
.mt-16 {
  margin-top: 16px;
}
.mr-24 {
  margin-right: 24px;
}
.mr-16 {
  margin-right: 16px;
}

.bold-700 {
  font-weight: 700;
}
.bold-600 {
  font-weight: 600;
}
.bold-300 {
  font-weight: 300;
}

.c-pointer {
  cursor: pointer;
}

.d-flex {
  display: flex;
}

.justify-between {
  justify-content: space-between;
}
.t-right {
  text-align: right;
}

.align-end {
  align-items: end;
}

.overflowX {
  overflow-x: auto;
}

.align-center {
  text-align: center;
}

.modal-responsive--title {
  margin-bottom: 16px;
}
.modal-responsive--subtitle {
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #6d7986;
  margin: 20px 0;
}


.title-3 {
  font-size: 18px;
  line-height: 26px;
  color: #172a3f;
}

.subtitle{
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
}
.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.top-menu {
  margin-top: 40px;
  margin-bottom: 26px;
  padding: 0 30px;
}

.top-menu > div {
  padding: 0 !important;
  margin-bottom: 4px;
}

.icon-button.bg-green {
  background-color: #d5f3e2;
  border: 1px solid #d5f3e2;
}

.icon-button.bg-blue {
  background-color: #e5f1ff;
  border: 1px solid #e5f1ff;
}

.icon-button.bg-red {
  background-color: #feebea;
  border: 1px solid #feebea;
}

.icon-button:hover {
  background-color: #fff;
}

.absolute-center {
  position: absolute;
  margin: 0;
  top: 50%;
  left: 50%;
  transform: translate(0, -50%);
}

.flex-end {
  display: flex;
  justify-content: end;
}

.sticky {
  position: sticky;
  top: 0;
  z-index: 1;
  background-color: #fff;
  padding: 16px 0;
}

.btn-circle {
  border-radius: 50px;
}

.btn-primary {
  background-color: var(--color-dark-bg-primary);
  color: var(--color-dark-bg-primary-inverted);
}

.text-line-through {
  -webkit-text-decoration-line: line-through; /* Safari */
  text-decoration-line: line-through;
}

.primary-color {
  color: #5fae50;
}

.mh-90 {
  min-height: 90vh;
}

.required:after {
  content: ' *';
  color: #f03b2f;
}


.mobile-only {
  display: none;
}

/* Extra small devices (phones, 599px and down) */
@media only screen and (max-width: 599px) {
  .mobile-block {
    display: block;
  }

  .mobile-only {
    display: block;
  }

  .desktop-only,
  .sidebar {
    display: none;
  }
}

/* Medium devices ( tablets, 600px and 1023px) */
@media only screen and (min-width: 600px) and (max-width: 1023px) {
  .table-pagination {
    display: flex;
    justify-content: space-between;
  }

  .sidebar {
    display: none;
  }
}

/* Extra large devices (large laptops and desktops, 1024px and up) */
@media only screen and (min-width: 1024px) {
  .table-pagination {
    display: flex;
    justify-content: space-between;
  }

  .desktop-only {
    display: block;
  }

  .sidebar {
    display: block;
  }
}
